@import "./common/global";
body {
	background: url(../img/bg.png);
	font-family: "Montserrat", sans-serif;
}

.main {
	&__body {
		padding: 25px 0;
		display: flex;
		flex-wrap: wrap;
	}
	&__items {
		display: flex;
	}
	.item {
		min-width: 250px;
		margin: 15px 15px;
		&__body {
			padding: 185px 20px;
			position: relative;
			z-index: 1;
			&::before {
				position: absolute;
				content: "";
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				background: rgba(#000, 0.15);
			}
		}
		&__btn {
			background: rgba(244, 249, 37, 0.75);
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
			border-radius: 10px;
			padding: 10px 38px;
			position: relative;
			z-index: 5;
			cursor: pointer;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			text-transform: uppercase;
			transition: all ease-in-out 0.1s;
			&:hover {
				background: rgba(244, 249, 37, 1);
			}
			&_fix {
				font-size: 16px;
				padding: 10px 0;
			}
		}
	}
	&__right {
		display: flex;
		flex-direction: column;
		align-items: center;
		.theme {
			display: flex;
			flex-direction: column;
			align-items: center;
			&__img {
				margin-bottom: 10px;
			}
			&__title {
				max-width: 228px;
				font-size: 24px;
				line-height: 29px;
				text-align: center;
				color: #000000;
				margin-bottom: 18px;
			}
		}
		.statistics {
			&__title {
				text-align: center;
				font-size: 16px;
				line-height: 20px;
				margin-bottom: 17px;
			}
			&__info {
				display: flex;
				justify-content: space-between;
				border-bottom: 1px solid #000;
				margin-bottom: 15px;
				min-width: 350px;
				span {
					display: inline-block;
				}
			}
		}
	}
}

.partners {
	&__body {
		padding: 26px 0;
		text-align: center;
	}
	&__title {
		font-weight: bold;
		font-size: 24px;
		line-height: 29px;
		color: #000000;
		margin-bottom: 55px;
	}
	&__items {
		display: flex;
		justify-content: space-between;
	}
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		max-width: 201px;
	}
	&__img {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 150px;
		height: 150px;
		background: #000000;
		border-radius: 50%;
		margin-bottom: 22px;
		&_white {
			background: #fff;
		}
	}
	&__info {
		font-size: 16px;
		line-height: 20px;
	}
}
//!Test
.test {
	position: absolute;
	visibility: hidden;
	opacity: 0;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 5;
	display: flex;
	justify-content: space-between;
	align-items: center;
	transition: all ease-in 0.2s;
	&__bg {
		background: rgba(#000, 0.4);
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 5;
	}
	&__body {
		background: white;
		padding: 40px;
		position: relative;
		z-index: 7;
	}
	&__title {
		font-size: 24px;
		margin-bottom: 20px;
		font-weight: 700;
	}
	&__text {
		line-height: 26px;
		margin-bottom: 20px;
	}
	&__btn {
		display: inline-block;
		background: #f4f925;
		border: 1px solid #f4f925;
		border-radius: 10px;
		padding: 6px 19px;
		font-weight: 500;
		font-size: 18px;
		line-height: 22px;
		color: #4758ae;
		text-transform: uppercase;
		cursor: pointer;
		transition: all ease-in 0.2s;
		&:hover {
			background: #4758ae;
			border: 1px solid #4758ae;
			color: #fff;
		}
	}
	&__close {
		position: absolute;
		right: 40px;
		font-size: 24px;
		cursor: pointer;
	}
}

//!training
.training {
	position: absolute;
	visibility: hidden;
	opacity: 0;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 5;
	display: flex;
	align-items: center;
	transition: all ease-in 0.2s;
	&__bg {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(#000, 0.15);
	}
	&__close{
		position: absolute;
		right: 40px;
		font-size: 24px;
		cursor: pointer;
	}
	&__body {
		position: relative;
		display: flex;
		justify-content: space-between;
		background: white;
		z-index: 7;
		padding: 40px;
	}
	&__main {
		max-width: 1000px;
		margin-right: 20px;
		text-align: left;
		.info {
			margin-bottom: 40px;
			&__title {
				font-size: 24px;
				font-weight: 700;
				margin-bottom: 40px;
			}
			&__quote {
				text-align: right;
				font-style: italic;
				line-height: 25px;
				margin-bottom: 40px;
				&-author {
					margin-top: 20px;
				}
			}
			&__text {
				line-height: 24px;
			}
		}
		.training-theme {
			&__title {
				font-size: 24px;
				font-weight: 700;
				margin-bottom: 40px;
			}
			&__list {
				li {
				}
				a {
					display: inline-block;
					margin-left: 20px;
					margin-bottom: 20px;
				}
			}
		}
	}
	&__aside{
		.aside{
			&__title{
				font-size: 24px;
				font-weight: 700;
				margin-bottom: 40px;
			}
			&__list{
				margin-bottom: 40px;
				li{
					margin-bottom: 20px;
				}
			}
		}
	}
}
.active {
	visibility: visible;
	opacity: 1;
}
