* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*:before,
*:after {
  box-sizing: border-box; }

:focus,
:active {
  outline: none; }

a:focus,
a:active {
  outline: none; }

nav,
footer,
header,
aside {
  display: block; }

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 16px;
  color: #333; }

input,
button,
textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit; }

.header {
  background: #4758ae; }
  .header__body {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 71px 0;
    position: relative; }
  .header__title {
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
    color: white; }
  .header__registration, .header__lang {
    position: absolute;
    top: 10px;
    right: 0; }
    .header__registration a, .header__lang a {
      color: white; }
  .header__registration {
    right: 90px; }
    .header__registration a {
      display: inline-block;
      background: #f4f925;
      border: 1px solid #f4f925;
      border-radius: 10px;
      padding: 6px 19px;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      color: #4758ae;
      text-transform: uppercase;
      transition: all ease-in .2s; }
      .header__registration a:hover {
        background: transparent;
        color: #fff; }
  .header__lang {
    top: 15px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    cursor: pointer; }

.imgf {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative; }
  .imgf > img {
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden; }

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px; }

.btn {
  display: inline-block;
  background: #f4f925;
  border: 1px solid #f4f925;
  border-radius: 10px;
  padding: 6px 19px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #4758ae;
  text-transform: uppercase;
  cursor: pointer;
  transition: all ease-in 0.2s; }
  .btn:hover {
    background: #4758ae;
    border: 1px solid #4758ae;
    color: #fff; }

body {
