.main .item {
  min-width: 250px;
  margin: 15px 15px; }
  .main .item__body {
    padding: 185px 20px;
    position: relative;
    z-index: 1; }
    .main .item__body::before {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.15); }
  .main .item__btn {
    background: rgba(244, 249, 37, 0.75);
