    border-radius: 10px;
    padding: 10px 38px;
    position: relative;
    z-index: 5;
    cursor: pointer;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    transition: all ease-in-out 0.1s; }
    .main .item__btn:hover {
