@import "../../../node_modules/normalize.css/normalize";
@import "./fonts";
@import "../mixins/fonts";
@import "../variables/variables";
@import "../nullstyle/nullstyle";
@import "footer";
@import "header";
.imgf {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	> img {
		width: 0;
		height: 0;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
		visibility: hidden;
	}
}

.container {
	max-width: 1230px;
	margin: 0 auto;
	padding: 0 15px;
}
.btn {
	display: inline-block;
	background: #f4f925;
	border: 1px solid #f4f925;
	border-radius: 10px;
	padding: 6px 19px;
	font-weight: 500;
	font-size: 18px;
	line-height: 22px;
	color: #4758ae;
	text-transform: uppercase;
	cursor: pointer;
	transition: all ease-in 0.2s;
	&:hover {
		background: #4758ae;
		border: 1px solid #4758ae;
		color: #fff;
	}
}
